import { Component, OnDestroy, OnChanges, OnInit, Input, HostListener } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { FormControl, FormGroup } from '@angular/forms';
import { BlogService } from '../blog.service';
import { Post } from '../blog.service';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.css']
})

export class EditComponent implements OnDestroy, OnInit, OnChanges {
	@Input() post: Post;

	postForm: FormGroup;

	constructor(
		private route: ActivatedRoute, 
		private blogService: BlogService,
		private router: Router,
	) {
		route.params.subscribe(() => this.updatePost());
		route.params.subscribe(() => this.getPost());
		this.postForm = new FormGroup ({
			titleControl: new FormControl(),
			bodyControl: new FormControl(),
		});
	}

	ngOnDestroy() {
		this.updatePost();
	}
	ngOnChanges() {
		this.updatePost();
	}

	ngOnInit() {
	}

	getPost() {
		const id = +this.route.snapshot.paramMap.get('id');
		let postReceived = this.blogService.getPost(id)
		if (postReceived == null) {
			this.router.navigate(['/']);
		}
		else {
			this.blogService.getPost(id).subscribe(post => this.post = post);
		}
	}

	deletePost() {
		this.blogService.deletePost(this.post.postid);
		this.post = null;
		this.router.navigate(['/']);
	}

	@HostListener('window:beforeunload')
	updatePost() {
		if (this.post) {
			if (!this.postForm.pristine) {
				this.blogService.updatePost(this.post);
				this.postForm.markAsPristine();
			}
		}
	}

	previewPost(): void {
		this.router.navigate(['/preview', this.post.postid]);
	}
}



