import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Parser, HtmlRenderer } from 'commonmark';

import { BlogService, Post } from '../blog.service';

@Component({
  selector: 'app-preview',
  templateUrl: './preview.component.html',
  styleUrls: ['./preview.component.css']
})
export class PreviewComponent implements OnInit {
  @Input() post: Post;
  convertedPostBody: string;
  convertedPostTitle: string;

  constructor(
    private route: ActivatedRoute,
    private blogService: BlogService,
    private router: Router
  ) {
    route.params.subscribe(() => this.getPost());
  }

  ngOnInit() {
  }

  getPost(): void {
    const id = +this.route.snapshot.paramMap.get('id');
    this.blogService.getPost(id).subscribe(post => this.post = post);
    this.convertMd(this.post.title, this.post.body);
  }

  convertMd(title: string, body: string): void {
    const reader = new Parser();
    const writer = new HtmlRenderer();

    const parsedTitle = reader.parse(title);
    this.convertedPostTitle = writer.render(parsedTitle);

    const parsedBody = reader.parse(body);
    this.convertedPostBody = writer.render(parsedBody);
  }

  editPost(): void {
    this.router.navigate(['/edit', this.post.postid]);
  }
}



